@import '../../_variables.css';

.listing-card {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-bottom: 32px;
    display: -ms-flexbox;
    display: flex;
    .image-preview {
        height: 200px;
        width: 320px;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        overflow: hidden;
        .all-photos {
            position: absolute;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
            -ms-flex-align: center;
                align-items: center;
            bottom: 0;
            right: 16px;
            background: rgba(0, 0, 0, 0.5);
            border: none;
            width: 54px;
            padding: 8px;
            padding-bottom: 12px;
            -webkit-transform: translateY(4px);
                -ms-transform: translateY(4px);
                    transform: translateY(4px);
            -webkit-transition: all 0.2s;
            -o-transition: all 0.2s;
            transition: all 0.2s;
            outline: none;
            &:hover {
                cursor: pointer;
                -webkit-transform: translateY(0);
                    -ms-transform: translateY(0);
                        transform: translateY(0);
                background: rgba(0, 0, 0, 1);
            }
            svg {
                color: #fff;
            }
            .photo-amount {
                color: #fff;
                font-weight: 500;

            }
        }
    }
    .card-content {
        padding: 24px;
        width: calc(100% - 360px);
        position: relative;
        .listing-header {
            display: -ms-flexbox;
            display: flex;
            -ms-flex-pack: justify;
                justify-content: space-between;
            width: 100%;
            margin-bottom: 16px;
            h2 {
                font-size: 24px;
                color: $dark;
            }
            .listing-title {
                font-weight: 500;
            }
            .listing-price {

            }
        }
        .stats {
            display: -ms-flexbox;
            display: flex;
            margin-bottom: 32px;
            .reference {
                margin-right: 16px;
            }
        }
        .features {
            .feature {
                position: relative;
                display: -ms-flexbox;
                display: flex;
                -ms-flex-align: center;
                    align-items: center;
                &:before {
                    display: block;
                    position: relative;
                    margin-right: 8px;
                    content: "";
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background: $primary;
                }
                margin-top: 8px;
            }
        }
        .details-button {
            position: absolute;
            bottom: 24px;
            right: 24px;
            border: none;
            background: $primary;
            color: #fff;
            border-radius: 4px;
            padding: 16px 64px;
            -webkit-box-sizing: border-box;
                    box-sizing: border-box;
            text-transform: uppercase;
            font-weight: 700;
            outline: none;
            -webkit-transition: all 0.2s;
            -o-transition: all 0.2s;
            transition: all 0.2s;
            text-decoration: none;
            &:hover {
                cursor: pointer;
                background: color($primary shade(20%));
            }
        }
    }
}



