.create-listing-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 64px;
    max-width: 800px;
    .dropzone {
        width: 100%;
        border-width: 2px;
        border-color: #3f51b5;
        border-style: dashed;
        border-radius: 5px;
        padding: 24px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        margin-bottom: 32px;
        &:hover {
            cursor: pointer;
        }
        .img-preview-list {
            display: -ms-flexbox;
            display: flex;
            overflow-x: scroll;
            .image-card {
                height: 100%;
                max-height: 250px;
                margin-right: 16px;
                .image-preview {
                    width: auto;
                    height: 250px;
                }
            }
        }
    }
    .row {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
        margin: 16px 0;
        .field, .FormControl {
            width: calc(50% - 16px);
        }
    }
    .row.fullwidth {
        width: 100%;
        .field, .FormControl {
            width: 100%;
        }
    }
    .feature-container {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .checkbox-group {
        width: calc(50% - 16px);
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
    }
    .field {
        margin-bottom: 24px;
    }
    .menu-container {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
        -ms-flex-align: center;
            align-items: center;
        width: calc(50% - 16px);
        h4 {
            font-weight: 400;
        }
    }
}



