@import '../../_variables.css';

.home {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    .hero {
        height: calc(100vh - 128px);
        width: 100%;
        /* background-image: url('./home.svg'); */
        background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255, 0.9)), to(transparent)), url('./background.jpeg') no-repeat center;
        background: -webkit-linear-gradient(rgba(255,255,255, 0.9), transparent), url('./background.jpeg') no-repeat center;
        background: -o-linear-gradient(rgba(255,255,255, 0.9), transparent), url('./background.jpeg') no-repeat center;
        background: linear-gradient(rgba(255,255,255, 0.9), transparent), url('./background.jpeg') no-repeat center;
        background-size: cover;
        /*background-image: url('./Gib.jpg');*/
        /* background-position: bottom;
        background-repeat: no-repeat;
        background-size: contain; */
        .animation-container {
            height: 400px;
            width: 100%;
            /* background-image: url('./animation.svg'); */
            background-size: contain;
            background-repeat: no-repeat;
        }
        .content {
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                    transform: translate(-50%, -50%);
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
            -ms-flex-pack: center;
                justify-content: center;
            -ms-flex-align: center;
                align-items: center;
            .title {
                font-size: 58px;
                color: $primary;
                font-family: 'Raleway', roboto, sans-serif;
                font-weight: 600;
                margin-bottom: 16px;
                /* text-shadow: 2px 4px 3px rgba(0,0,0,0.3); */
            }
            .subtitle {
                color: #424242;
                font-size: 32px;
                margin-bottom: 80px;
                text-shadow: 1px 1px 0.5px rgba(0,0,0,0.3);
            }
        }
    }

    section {
        padding-top: 40px;
    }
}



